.address {
  width: 33%;
  margin-right: 25px;
  min-width: 230px;
}

.address__list {
  @include list-reset();
}

.address__text {
  color: $dark-grey;
  font-family: 'PT Mono';
  @include font(15px, 25px);
  font-weight: 400;
  margin-bottom: 0;
}

.address__link {
  @include link-reset($dark-grey);
  font-family: 'PT Mono';
  @include font(15px, 25px);
  font-weight: 400;

  &:hover {
    color: $blue;
  }

  &:focus {
    outline: 1px solid $blue;
  }

  &:active {
    color: $blue-active;
  }
}

@media (min-width: $tablet-min-width) and (max-width: $tablet-max-width) {
  .address {
    width: 50%;
    margin-left: 0;
    margin-right: 32px;
  }
}

@media (min-width: 0) and (max-width: $mobile-max-width) {
  .address {
    width: 100%;
    margin-right: 0;
  }

  .address__text {
    margin-top: 0;
  }

  .address__wrapper {
    margin-right: 10px;
    margin-left: 15px;
    display: flex;
    align-items: center;
  }

  .address__list {
    margin-right: 15px;
    margin-left: 15px;
    margin-top: 3px;
    padding-bottom: 27px;
    padding-right: 5px;
    display: none;
  }
}
